@tailwind base;
@tailwind components;
@tailwind utilities;


@layer components {
    :root {
        --tw-text-opacity: 1;
        --tw-bg-opacity: 1;
        --tw-gradient-from: #df0000;
        --tw-gradient-to: #550089;
        --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
        --tw-border-opacity: 1;
    }

    html {
        font-family: "Poppins", sans-serif;
    }

    body {
        margin: 0;
        line-height: inherit;
        background-color: black;
    }

    .links {
        background: -webkit-linear-gradient(left, #ff0000, #9e00ff);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        transition: 400ms;
    }

    .links:hover {
        background-size: 300% 100%;
    }

    .socialbtn {
        display: flex;
        height: 2.5rem;
        width: 2.5rem;
        align-items: center;
        justify-content: center;
        border-radius: 0.5rem;
        background-color: rgb(33 36 37 / var(--tw-bg-opacity));
        cursor: pointer;
        transition: 300ms;
    }

    .socialbtn:hover {
        background-image: linear-gradient(to right, var(--tw-gradient-stops));
        color: white;
    }

    .menu-item {
        display: flex;
        height: 5rem;
        width: 5rem;
        cursor: pointer;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background-color: rgb(33 36 37 / var(--tw-bg-opacity));
        color: rgb(166 166 166 / var(--tw-text-opacity));
        font-family: Poppins, sans-serif;
        font-size: .8125rem;
        font-weight: 500;
        transition: 300ms;
    }

    @media screen and (max-width: 1024px) {
        .menu-item {
            width: 4rem;
            height: 4rem;
        }
    }

    .menu-active,
    .menu-item:hover {
        display: flex;
        cursor: pointer;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background-image: linear-gradient(to right, var(--tw-gradient-stops));
        color: rgb(255 255 255 / var(--tw-text-opacity));
        font-size: .8125rem;
        font-weight: 500;
    }

    .menu-toggle-btn {
        margin-left: 0.75rem;
        display: flex;
        height: 40px;
        width: 40px;
        cursor: pointer;
        align-items: center;
        justify-content: center;
        border-radius: 9999px;
        background-color: rgb(255 255 255 / var(--tw-text-opacity));
        color: rgb(0 0 0 / var(--tw-text-opacity));
        transition-property: all;
        transition-duration: 300ms;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    .menu-toggle-btn-active {
        background-image: linear-gradient(to left, var(--tw-gradient-stops));
        color: rgb(255 255 255 / var(--tw-text-opacity));
    }

    .mobile-menu-items {
        display: flex;
        cursor: pointer;
        align-items: center;
        padding: 0.625rem 1rem;
        font-size: .8125rem;
        font-weight: 500;
        color: rgb(255 255 255 / var(--tw-text-opacity));
        transition-duration: 300ms;
    }

    .mobile-menu-items-active {
        color: #ff2222;
    }

    .page-title-headline {
        color: rgb(255 255 255 / var(--tw-text-opacity));
        font-size: 2.6rem;
        font-weight: 600;
    }

    .page-title-headline::after {
        height: 0.125rem;
        border-radius: 0.375rem;
        background-image: linear-gradient(to right, var(--tw-gradient-stops));
        content: var(--tw-content);
        width: 10rem;
    }
}

/* SLICK SLIDER */
.slick-slider {
    background: linear-gradient(to left, #7c7c7c81, #2929297c);
}

.slick-track {
    display: flex !important;
    align-items: center;
}

.slick-slide img {
    filter: contrast(2);
    transition: 400ms;
}

.slick-slide img:hover {
    transform: scale(1.2);
}

/* SCROLL BAR */
body::-webkit-scrollbar,
.modal::-webkit-scrollbar {
    width: 0.75em;
}

body::-webkit-scrollbar-thumb {
    background-color: rgb(37, 37, 37);
    border-radius: 50px;
}

.modal::-webkit-scrollbar-thumb  {
    padding-top: 1em;
    background-color: rgba(255, 255, 255, 0.616);
    border-radius: 50px;
}